// We should try to reuse the user page HTML for groups when possible
// To style group content differently, use the existing classes with a .group parent class.
// For example: .group .user-secondary-navigation

span.mention-group {
  color: var(--primary-high-or-secondary-low);
  padding: 2px 4px;
  border-radius: 8px;
  font-weight: 600;
  font-size: $font-down-1;
}

.group-details-container {
  background: var(--primary-very-low);
  padding: 20px;
  margin-bottom: 15px;
  position: relative;
}

.group-outlet {
  position: relative;
}

.group-members-actions {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  input + .group-members-manage {
    margin-left: auto;
  }

  .group-username-filter {
    margin: 0 0 5px 0;
    vertical-align: middle;
  }
}

.group-info {
  width: 100%;

  .group-info-name {
    font-size: $font-up-3;
    font-weight: bold;
    color: var(--primary);
  }

  .group-info-full-name {
    font-size: $font-up-2;
    color: var(--primary-high-or-secondary-low);
  }

  span {
    display: inline-block;
    vertical-align: middle;
  }

  $size: 50px;
  $icon-size: $size / 1.8;

  .avatar-flair-image {
    width: $size;
  }

  .avatar-flair {
    background-size: $size;
    height: $size;
    width: $size;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;

    .d-icon {
      height: $icon-size;
      width: $icon-size;
    }
  }
}

.group-manage-logs-filter {
  margin-right: 10px;

  &:hover {
    background-color: var(--danger);
  }
}

table.group-manage-logs {
  width: 100%;

  td {
    padding: 0.5em;
  }

  .group-manage-logs-expand-details {
    cursor: pointer;

    .d-icon {
      color: var(--primary-medium);
    }
  }
}

table.group-members {
  width: 100%;
  table-layout: fixed;

  th:first-child {
    width: 30%;
    text-align: left;
  }

  th:last-child {
    width: 5%;
  }

  th.group-members-actions {
    width: 5%;
  }

  th {
    text-align: center;
  }

  tr {
    .user-info {
      display: block;
      .avatar-flair {
        color: var(--primary);
      }
    }

    td:first-child {
      text-align: left;
    }

    td {
      text-align: center;
      color: var(--primary-medium);
      padding: 0.8em 0;
    }
  }
}

.group-owner-label {
  color: var(--primary);
}

.group-flair-inputs {
  display: inline-block;

  .avatar-flair-preview {
    position: relative;
    width: 45px;

    .avatar-wrapper {
      background-color: var(--primary-very-low);
    }
  }

  .radios {
    margin-bottom: 5px;
  }

  .radio-label {
    display: inline-flex;
    padding-right: 10px;
    margin-bottom: 0;
    align-items: center;
  }

  .uploaded-image-preview {
    height: 75px;
    width: 275px;
  }
}

.group-form-save {
  margin-right: 20px;
}

.group-add-members {
  .ac-wrap {
    overflow: hidden;
  }

  .group-add-members-make-owner {
    label {
      display: inline;
      vertical-align: middle;
    }

    input[type="checkbox"] {
      vertical-align: middle;
    }
  }
}

label.group-category-permissions-desc {
  font-size: 1.15em;
  margin-bottom: 1em;
}

table.group-category-permissions {
  width: 100%;

  tr {
    line-height: 3em;
    width: 100%;

    .category-name {
      font-size: 1.25em;
    }
  }
}
